*{
  box-sizing: border-box;
}
.mainBox {
  height: 100%;
}
.trigger {
  padding: 0 24px;
  font-size: 18px;
  line-height: 64px;
  cursor: pointer;
  transition: color 0.3s;
}

.trigger:hover {
  color: #1890ff;
}

.logo {
  height: 32px;
  margin: 16px;
  background: rgba(255, 255, 255, 0.3);
}

.sider-header{
  padding: 20px 24px;
  display: flex;
  flex-direction: row;
  align-items: flex-end;
}

.site-layout .site-layout-header {
  display: flex;
  background: #fff;
  padding: 0;
  flex-direction: row;
  align-items: center;
}
.site-layout .site-layout-content {
  display: flex;
  margin: 16px;
  flex-direction: row;
  background-color: #f0f2f5;
  flex-wrap: wrap;
  overflow: auto;
}

.ant-menu .room-menu-item {
  margin: 0;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.loadBox {
  position: absolute;
  top: 50%;
  left: 50%;
  margin-right: -50%;
}

.addBtn {
  position: fixed;
  bottom: 20px;
  right: 20px;
}

.delBtn {
  position: fixed;
  bottom: 20px;
  right: 75px;
}

.commandBtn {
  position: fixed;
  bottom: 20px;
  right: 130px;
}

.ant-layout-content {
  flex: none;
}

.ant-popover {
  position: fixed;
}

.audio-wave {
  display: none;
}

.swatch{
  border-radius: 20px;
  width: 22px;
  height: 22px;
  border: 1px solid rgb(172, 172, 172);
  cursor: pointer;
}
.color-picker{
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin-bottom: 20px;
}
.color-picker h4{
  margin-bottom: 0;
}
.ant-popover-inner-content .react-colorful{
  width: 150px;
  height: 150px;
}
.popover {
  position: absolute;
  top: 255px;
  border-radius: 9px;
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.15);
  z-index: 1;
}